@import url(https://fonts.googleapis.com/css?family=Roboto);
.sidebar {
  height: 100%;
  width: 150px;
  background: rgba(16, 16, 34, 0.9);
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px; }
  @media (max-width: 650px) {
    .sidebar {
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 125px; } }
  @media (max-height: 500px) {
    .sidebar {
      position: fixed;
      top: 0;
      left: 0;
      height: 100%; } }
  .sidebar__navigation {
    margin: 0;
    padding: 0;
    list-style: none; }
  .sidebar__controls {
    background: #101022;
    margin: 0;
    padding: 15px;
    list-style: none;
    height: 40px;
    border-top-left-radius: 7px;
    display: flex;
    align-items: center; }
  .sidebar__control {
    height: 10px;
    width: 10px;
    border-radius: 50%;
    margin-right: 10px;
    display: block;
    padding: 0;
    border: none;
    outline: none;
    position: relative; }
    .sidebar__control:before, .sidebar__control:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 1px;
      opacity: 0; }
    .sidebar__control--close {
      background: #ff594f; }
      .sidebar__control--close:before, .sidebar__control--close:after {
        background: #550103;
        height: 7px;
        width: 1px;
        transform: translate(-50%, -50%) rotate(45deg); }
      .sidebar__control--close:after {
        transform: translate(-50%, -50%) rotate(-45deg); }
    .sidebar__control--minimize {
      background: #ffbc1b; }
      .sidebar__control--minimize:before {
        height: 1px;
        width: 6px;
        background: #4d0c00; }
    .sidebar__control--maximize {
      background: #00cd46; }
      .sidebar__control--maximize:before, .sidebar__control--maximize:after {
        background: #00671a;
        height: 6px;
        width: 1px; }
      .sidebar__control--maximize:after {
        height: 1px;
        width: 6px; }
    .sidebar__control:hover:before, .sidebar__control:hover:after, .sidebar__control:focus:before, .sidebar__control:focus:after {
      opacity: 1; }

.nav-button {
  font-size: 14px;
  color: #ffffff;
  padding: 17px 15px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background: none;
  border: none; }
  .nav-button__icon {
    fill: #ffffff; }
  .nav-button__count {
    margin: 0;
    min-width: 13px;
    color: #8a8995; }
  .nav-button__label {
    margin: 0;
    flex-grow: 1;
    text-align: left;
    text-transform: capitalize;
    padding-left: 9px; }
  .nav-button:hover, .nav-button:focus {
    background: #292a48;
    outline: none; }
  .nav-button:active {
    background: #323358; }
    .nav-button:active .nav-button__count {
      color: #ffffff; }

.issue {
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 11px 12px; }
  .issue:not(:last-child) {
    margin-bottom: 5px; }
  .issue__label {
    margin: 0;
    font-size: 15px;
    line-height: 16px; }
  .issue__icon {
    display: inline-block;
    flex-shrink: 0;
    cursor: pointer;
    transition: 0.3s all; }
    .issue__icon:hover, .issue__icon:focus {
      fill: #575999;
      stroke: none;
      outline: none; }
    .issue__icon--open {
      fill: #ffffff;
      stroke: #e0e0e0; }
      .issue__icon--open:hover, .issue__icon--open:focus {
        fill: #c5c6de;
        stroke: none; }

.content {
  background-color: #f6f6f6;
  padding: 10px 5px 0 15px;
  flex-grow: 1;
  height: 100%;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px; }
  @media (max-height: 500px) {
    .content {
      margin-left: 150px; } }
  @media (max-width: 650px) {
    .content {
      margin-left: 125px;
      padding-left: 5px; } }

.register__list {
  margin: 0;
  padding: 0;
  list-style: none;
  height: 100%;
  overflow: auto; }

.record__heading {
  font-size: 12px;
  font-weight: normal;
  line-height: 1;
  color: #8a8995;
  margin: 0 0 9px; }

.record__issues-list {
  margin: 0;
  padding: 0 13px 10px 0;
  list-style: none; }
  @media (max-width: 650px) {
    .record__issues-list {
      padding-right: 5px; } }

.window {
  height: 500px;
  width: 650px;
  display: flex;
  border-radius: 7px;
  overflow: hidden;
  position: relative;
  z-index: 1; }
  @media (max-width: 650px) {
    .window {
      height: 100vh; } }
  @media (max-height: 500px) {
    .window {
      width: 100vw; } }

.mask {
  position: absolute;
  top: calc((100vh - 500px) / -2);
  left: calc((100vw - 650px) / -2);
  height: 100vh;
  width: 100vw;
  transform: scale(1.03);
  filter: blur(4px);
  -webkit-filter: blur(4px);
  z-index: -1; }
  @media (max-width: 650px) {
    .mask {
      left: 0; } }
  @media (max-height: 500px) {
    .mask {
      top: 0; } }

.app {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }

.background {
  background-color: #e0e0e0;
  background-image: url(/el-react-app/static/media/background.e22ac107.jpg);
  background-size: cover;
  background-position: right center;
  background-repeat: no-repeat; }

